@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.drumContainer {
    overflow: hidden;
    position: relative;
    height: 18px;
    width: 100%;
}

.drum {
    font-size: 18px;
}

.hide {
    opacity: 0;
    animation: fadeIn 0.5s ease-in 0.5s forwards;
}
